<div class="page-material-list">

  <div class="flex-v-center tool-bar">
  <div class="flex-v-center search-bar" style="margin-right: 20px;">
    <i class="icon f-20 c-8">search</i>
    <select v-model="filter.status" class="f-14" @change="$refs.list.update(true)">
      <option v-for="item in allStatus" :value="item.value" v-bind:key="item.id">{{item.text}}</option>
    </select>
  </div></div>
  <div class="flex-item scroll-y">
    <data-list ref="list" :page-size="10"  :param="filter" url="/haolifa/applyBuy/list" method="get">
      <tr slot="header">
        <th style="width: 60px;"></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th class="t-right" style="width: 80px;"></th>
      </tr>
      <!-- item: ; index:  -->
      <template slot="item" slot-scope="{ item, index }">
        <td class="c-a">{{index}}</td>
        <td>{{item.productOrderNo}}</td>
        <td>{{item.materialName}}</td>
        <td>{{item.materialGraphNo}}</td>
        <td>{{item.purchaseNumber}}</td>
        <td>{{item.createTime}}</td>
        <td>{{item.arrivalTime}}</td>
        <td>{{allStatus[item.status].text}}</td>
        <td class="t-right">
          <a href="javascript:;" v-if="item.status==2" style="margin-right: 3px" class="blue" @click="dealApplyBuy(item.id)"></a>
        </td>
      </template>
    </data-list>
  </div>
</div>
</template>

<script>
import DataList from '@/components/datalist'
export default {
  name: 'page-order-list',
  components: { DataList },
  data () {
    return {
      filter: {
        type:0,
        status: -1
      },
      allStatus: [
          {value: -1, text:''},
          {value: 1, text: ''},
          {value: 2, text: ''},
          {value: 3, text: ''},
          {value: 4, text: ''},
      ]
    }
  },
  methods: {
      dealApplyBuy(itemId) {
        this.$http.post(`/haolifa/applyBuy/updateStatus/${itemId}`).then(res=>{
            this.$toast("")
            this.$refs.list.update();
        }).catch(e=>{
            this.$toast("")
        })
      }
  }
}
</script>

