</template>

<script>
    import DataList from '@/components/datalist'
    export default {
        name: 'spray-add-list',
        components: {DataList},
        data() {
            return {
                items:[],
                completeLayer:false,
                layer:false,
                filter: {
                    type:1,
                    status: -1
                },
                rowStatusList: [
                    {status: 0, name: ''},
                    {status: 1, name: ''},
                    {status: 2, name: ''},
                    {status: 3, name: ''},
                    {status: 4, name: ''}
                ],
                statusList: [
                    {status: 0, name: ''},
                    {status: 1, name: ''},
                    {status: 2, name: ''},
                    {status: 3, name: ''},
                    {status: 4, name: ''},
                    {status: -1, name: ''}
                ],
                inspectHistoryAdd:{},
                spray:{
                    planner:'',
                    sprayNo:'',
                    createTime:'',
                    items:[]
                }
            }
        },
        methods: {
            completeInspect(item){
                this.updateStatus(item.sprayNo, 2)
            },
            complete() {
                this.$http.post(`/haolifa/spray/inspect`,this.inspectHistoryAdd).then(res=>{
                    this.$toast("")
                    this.completeLayer = false;
                    this.$refs.list.update();
                }).catch(e=>{
                    this.$toast(e.msg || e.message);
                })
            },
            addSprayInspect(item){
                this.inspectHistoryAdd={
                    sprayNo:item.sprayNo,
                    originalGraphNo:item.materialGraphNo,
                    materialGraphNo:'',
                    materialGraphName:'',
                    testNumber:0,
                    qualifiedNumber:0,
                    unqualifiedNumber:0,
                    handlingSuggestion:'',
                    remark:''
                }
                this.completeLayer = true;
            },
            updateStatus(sprayNo, status) {
                this.$http.put(`/haolifa/spray/status/${sprayNo}/${status}`).then(res=>{
                    this.$refs.list.update();
                }).catch(e=>{
                    this.$toast(e.msg || e.message)
                })
            },
            // 
            sprayInfo(item) {

                // 
                this.$http.get(`/haolifa/spray/form/${item.sprayNo}`).then(res=>{
                    this.spray = res;
                }).catch(e => {
                    this.$toast(e.msg || e.message)
                });
                // 
                this.$http.get(`/haolifa/spray/inspect/list/${item.sprayNo}`).then(res=>{
                    this.inspectHistory = res;
                }).catch(e => {
                    this.$toast(e.msg || e.message)
                });
                this.layer = true;
            }
        }
    }
</script>

